import {IProject} from "../models/Project";
import {apiRequest, HttpRequestMethod} from "./api-service";
import {getSession} from "../auth/AuthService";

// const API_URL = "http://localhost:8080/v1/";
const API_URL = "https://api.ClearDomain.co.uk/v1/";
export const getAllProjects = async (): Promise<IProject[]> => {
    const url = API_URL + "projects";
    const session = await getSession();
    if (session && session.getIdToken()){
        const accessToken = session.getIdToken()
        return await apiRequest<IProject[]>(url, {
            method: HttpRequestMethod.GET,
            token: accessToken.getJwtToken()
        });
    } else {
        console.error();
        return []
    }

}
